export default function Reset2() {
    const classes = useStyles();
    const [password, setPassword] = useState(false);
    const [copassword, setCoPassword] = useState(false);
    const [passwordErr, setpasswordErr] = useState(false);
    const [passwordErr1, setpasswordErr1] = useState(false);


    const history = useHistory();
    function handelpassword(e) {
        let item = e.target.value;

        let passregix = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{8,16}$/
        if (passregix.test(item)) {
            setpasswordErr(false)
            console.log('valid password')
            setPassword(true)
        }
        else if (item.length === 0) {
            setpasswordErr(true)
            console.log('Empty Password')
        }
        else {
            setpasswordErr(true)
            console.log('invalid password')
        }
    }
    function handelcopassword(e) {
        let item2 = e.target.value;
        let a = document.getElementById('pass').value;
        if (a === item2) {
            setpasswordErr1(false)
            console.log('password match')
            setCoPassword(true)
        }
        else {
            setpasswordErr1(true)
            console.log('invalid password')
        }
    }
    function save(e) {
        if (password === true && copassword === true) {
            history.push('/login')
        }
        else {
            alert("Fill correct data")
            e.preventDefault();
            return false;
        }

    }
    return (
        <div className={classes.loginbackground}>
            <div>
                <img src={logo} alt="" className={classes.logoimg} />
            </div>
            <Grid container className={classes.maingrid}>
                <Grid item xs={6} className={classes.firstgrid}>
                    <img src={welcome} alt="" className={classes.welcomeimg} />
                </Grid>
                <Grid item xs={6} className={classes.loginform} alignItems='center'>
                    <div>
                        <div>
                            <h1 className={classes.logintext}>RESET PASSWORD</h1>
                        </div>
                        <div>
                            <p className={classes.signin}>If you forgot your password, well, then well email you<br /> instructions to reset your password.</p>
                        </div>
                        <div className={classes.usernamediv}>
                            <input type='password' id='pass' placeholder='New password' className={classes.username}
                                onChange={handelpassword} />
                            <br />
                            {passwordErr ? <span style={{ color: 'red' }}>Please Enter valid Password e.g :<br /> "Abc@1234"</span> : ""}

                        </div>
                        <div className={classes.passworddiv}>
                            <input type='password' placeholder='Confirm password' className={classes.password}
                                onChange={handelcopassword} />
                            <br />
                            {passwordErr1 ? <span style={{ color: 'red' }}>Password not match</span> : ""}
                        </div>

                        <div className={classes.logindiv}>
                            <button type='submit'
                                onClick={save} className={classes.loginbtn}>Save</button>
                        </div>
                    </div>
                </Grid>
            </Grid>
        </div>
    )
}
